import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import { Redirect } from "react-router-dom";

import AssetFiscalUpdateForm from "./updateForm";

import notification from "../../../../components/notification";
import { Spin } from "antd";

import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/asset/assetFiscal/actions";
import { Box } from "../../../App/app.style";


const redirectTo = "/dashboard/setup/asset-fiscals"

class UpdateAssetFiscal extends Component {
  // Get All data diawal
  componentDidMount() {
    const { editAssetFiscal } = this.props;
    const { id } = this.props.match.params;
    editAssetFiscal(id);
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.assetFiscal.error) {
      notification("error", this.props.assetFiscal.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.assetFiscal.success) {
      this.props.history.replace(redirectTo)
    }
  }

  handleSubmit = value => {
    const { updateAssetFiscal } = this.props;
    const { id } = this.props.match.params;

    // Uppercasing location code
    //value.depreciation_rate = value.depreciation_rate/100;
    updateAssetFiscal(id, value);
  };

  render() {

    // console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader title={<IntlMessages id="assetFiscals.update.title" />}
            style={{ margin: 0 }}
          >
            <Button onClick={() => this.props.history.replace(redirectTo)}>
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          {this.props.assetFiscal.loadingEdit ? (
            <div className="spinStyle">
              <Spin size="large" />
            </div>
          ) : (
              <AssetFiscalUpdateForm
                auth={this.props.auth}
                submit={this.handleSubmit}
                assetFiscal={this.props.assetFiscal}
              />
            )}
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    auth: state.auth,
    assetFiscal: state.assetFiscal
  };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(UpdateAssetFiscal);
