const actions = {
  getAssetFiscals: (page, search, filter) => ({
    type: 'GET_ASSET_FISCALS_REQUEST',
    payload: {
      page: page,
      search: search,
      filter: filter
    }
  }),
  createAssetFiscal: () => ({
    type: 'CREATE_ASSET_FISCAL_REQUEST'
  }),
  storeAssetFiscal: data => ({
    type: 'STORE_ASSET_FISCAL_REQUEST',
    payload: { body: data }
  }),
  editAssetFiscal: id_asset_fiscal => ({
    type: 'EDIT_ASSET_FISCAL_REQUEST',
    payload: { id_asset_fiscal: id_asset_fiscal }
  }),
  updateAssetFiscal: (id_asset_fiscal, data) => ({
    type: 'UPDATE_ASSET_FISCAL_REQUEST',
    payload: {
      id_asset_fiscal: id_asset_fiscal,
      body: data
    }
  }),
  showAssetFiscal: id_asset_fiscal => ({
    type: 'SHOW_ASSET_FISCAL_REQUEST',
    payload: { id_asset_fiscal: id_asset_fiscal }
  }),
  deactivateAssetFiscal: id_asset_fiscal => ({
    type: 'DEACTIVATE_ASSET_FISCAL_REQUEST',
    payload: { id_asset_fiscal }
  }),
  activateAssetFiscal: id_asset_fiscal => ({
    type: 'ACTIVATE_ASSET_FISCAL_REQUEST',
    payload: { id_asset_fiscal }
  }),
  clearSuccess: () => ({
    type: "CLEAR_SUCCESS"
  }),
  clearError: () => ({
    type: "CLEAR_ERROR"
  }),
  clearErrorField: () => ({
    type: "CLEAR_ERROR_FIELD"
  })
};

export default actions;
